<template>
  <div>
    <div class="flex column">
      <div class="flex content-between">
        <label for="speechInput">🗣 Parle, je t'écoute :</label>
        <button 
        class="button2"
          @mousedown="startRecording" 
          @mouseup="stopRecording" 
          @mouseleave="stopRecording" 
          :class="{ recording: isRecording }"
          :disabled="!recognition"
        >
          🎤
        </button>
      </div>
      
      <textarea id="speechInput" class="white-input" type="text" v-model="text" readonly />
    </div>
    
    <p v-if="!recognition" style="color: red;">🚨 Votre navigateur ne supporte pas la reconnaissance vocale.</p>
  </div>
</template>

<script>
export default {
data() {
  return {
    text: "",
    recognition: null,
    isRecording: false
  };
},
mounted() {
  if (window.SpeechRecognition || window.webkitSpeechRecognition) {
    this.recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition)();
    this.recognition.continuous = false;
    this.recognition.interimResults = false;
    this.recognition.lang = "fr-FR";
    this.recognition.onresult = (event) => {
      this.text = event.results[0][0].transcript;
    };
  }
},
methods: {
  startRecording() {
    if (!this.recognition) {
      alert("Votre navigateur ne supporte pas la reconnaissance vocale.");
      return;
    }
    this.recognition.start();
    this.isRecording = true;
  },
  stopRecording() {
    if (this.recognition && this.isRecording) {
      this.recognition.stop();
      this.isRecording = false;
    }
  }
}
};
</script>

<style>
label {
  text-align: left;
  display: block;
  margin-bottom: 16px;
}
.flex {
  display: flex;
}
.flex.column {
  flex-direction: column;
}
.flex.content-between{
  justify-content:space-between;
  align-items:center;
}
.button2 {
  background-color: #42b983;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 50%;
  font-size: 20px;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

button.recording {
  background-color: red;
  transform: scale(1.2);
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.6);
}
textarea{
  min-height:50px;
}
</style>