<template>
    <div>
    <NotificationService ref="notification" />
      <button @click="sendCustomNotification">Envoyer une notification</button>
    </div>
  </template>
  
  <script>
  import NotificationService from "@/components/NotificationService.vue"; // Vérifie ce chemin
  
  export default {
    components: { NotificationService },
    methods: {
        sendCustomNotification() {
            this.$refs.notification.sendNotification("Welcome :) !", "Une notification utile rien que pour toi ! :)");
        }
    }
  };
  </script>
  <style>
    button {
    margin: 5px;
    padding: 10px;
    background-color: #42b983;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .white-input{
    background:#FFF;
    border-radius:5px;
    border:1px solid #a3a3a3;
    height:25px;
  }
</style>