<template>
  <div>
    <button @click="fetchData">📡 Appeler API</button>
    <p v-if="data">Données : {{ data }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: null
    };
  },
  methods: {
    async fetchData() {
      if (!navigator.onLine) {
        alert("Pas de connexion. La requête sera envoyée plus tard.");
        await this.storeRequest("https://jsonplaceholder.typicode.com/todos/1", { method: "GET" });

        if ('serviceWorker' in navigator && 'SyncManager' in window) {
          const registration = await navigator.serviceWorker.ready;
          registration.sync.register("sync-api-request").then(() => {
            console.log("📡 Background Sync enregistré !");
          }).catch(err => {
            console.error("❌ Échec de l'enregistrement du Background Sync", err);
          });
        }
        return;
      }
      
      try {
        const response = await fetch("https://jsonplaceholder.typicode.com/todos/1");
        const result = await response.json();
        this.data = result.title;
      } catch (error) {
        console.error("Erreur API", error);
      }
    },
    
    async storeRequest(url, options) {
      const db = await openDatabase();
      const tx = db.transaction("requests", "readwrite");
      const store = tx.objectStore("requests");
      await store.add({ url, options });
    }
  },
  mounted() {
    window.addEventListener("online", async () => {
      const db = await openDatabase();
      const tx = db.transaction("requests", "readonly");
      const store = tx.objectStore("requests");

      // Assurez-vous que `store.getAll()` retourne bien une promesse
      const requests = await new Promise((resolve, reject) => {
        const getAllRequest = store.getAll();
        getAllRequest.onsuccess = () => resolve(getAllRequest.result || []);
        getAllRequest.onerror = (event) => reject(event.target.error);
      });

      if (!Array.isArray(requests)) {
        console.error("Erreur: `requests` n'est pas un tableau:", requests);
        return;
      }

      for (const request of requests) {
        try {
          const response = await fetch(request.url, request.options);
          const result = await response.json();
          this.data = result.title;

          if ("Notification" in window && Notification.permission === "granted") {
            new Notification("✅ API Réponse !", {
              body: "Les données ont été récupérées avec succès.",
              icon: "/icons/icon-192x192.png"
            });
          }

          // Supprime la requête exécutée
          const deleteTx = db.transaction("requests", "readwrite");
          deleteTx.objectStore("requests").delete(request.id);
        } catch (error) {
          console.error("Erreur de synchronisation API", error);
        }
      }
    });
  }

};

function openDatabase() {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open("offline-requests", 1);
    request.onerror = (event) => reject(event.target.error);
    request.onsuccess = (event) => resolve(event.target.result);
    request.onupgradeneeded = (event) => {
      const db = event.target.result;
      if (!db.objectStoreNames.contains("requests")) {
        db.createObjectStore("requests", { keyPath: "id", autoIncrement: true });
      }
    };
  });
}
</script>